*{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

body{
    padding: 60px;
}

input{
    width: 100%;
    font-size: 1.2 rem;
    color: gray;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden; /*para que los border de la img no salgan del contenedor */
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p{
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}